.header-container {
  font-family: 'Gilroy';
  position: fixed;
  top: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 80px;
  padding: 0 60px;
  z-index: 99;
  background: rgba(2, 5, 16, 0.5);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid #3A3B41;

  .logo-container {
    display: flex;
    align-items: center;
    cursor: pointer;

    .logo {
      height: 40px;
    }

    .title {
      color: #fff;
      font-weight: bold;
      font-size: 24px;
      line-height: 28px;
      margin-left: 11px;
    }
  }

  .links {
    color: #fff;
    display: flex;
    flex-direction: row;

    .link {
      opacity: 0.6;
      font-size: 20px;
      line-height: 23px;

      & + .link {
        margin-left: 26px;
      }

      &:hover {
        cursor: pointer;
        opacity: 0.8;
      }

      &.active {
        opacity: 0.8;
      }
    }
  }

  .user-profile {
    height: 50px;
    background: #10121A;
    border: 0.6px solid #1C1E25;
    border-radius: 16px;
    display: flex;
    padding-left: 15px;
    padding-right: 13px;
    align-items: center;

    &:hover {
      background: #1C1E25;
    }

    .username {
      color: #fff;
      font-size: 14.5px;
      line-height: 17px;
      max-width: 130px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    .user-avatar {
      border-radius: 50%;
      height: 24px;
      width: 24px;
      margin-left: 20px;
    }
  }

  .launch-btn {
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 160%;

    .launch-btn-icon {
      display: flex;
      margin-right: 13px;
    }
  }
}

.user-profile-dropdown {
  font-family: 'Gilroy';
  display: flex;
  flex-direction: column;

  background: #10121A;
  border: 0.6px solid rgba(255, 255, 255, 0.16);
  border-radius: 9px;

  padding: 30px 35px;
  margin-top: 10px;
  text-align: center;

  .item {
    color: rgba(255, 255, 255, 0.5);
    font-size: 15px;
    line-height: 18px;
    user-select: none;

    &:not(.disabled):hover {
      color: rgba(255, 255, 255, 0.9);
      cursor: pointer;
    }

    &+.item {
      margin-top: 18px;
    }
  }

  .logout-btn {
    font-weight: 500;
    opacity: 0.85;

    &:hover {
      cursor: pointer;
      opacity: 1;
    }
  }
}